import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { ProductsComponent } from './products/products.component';
import { ProfileComponent } from './profile/profile.component';
import { SignupSigninComponent } from './signup-signin/signup-signin.component';

import { AuthGuard } from './guard/auth.guard';
import { UnauthGuard } from './guard/unauth.guard';
import { PaymentComponent } from './payment/payment.component';
import { FavouriteComponent } from './favourite/favourite.component';

const routes: Routes = [
  {path:'auth',component:SignupSigninComponent,canActivate:[UnauthGuard]},
  {path:'',component:HomeComponent,canActivate:[AuthGuard]},
  {path:'product',component:ProductsComponent,canActivate:[AuthGuard]},
  {path:'paymentHistory',component:PaymentComponent,canActivate:[AuthGuard]},
  {path:'favourite',component:FavouriteComponent,canActivate:[AuthGuard]},
  {path:'profile',component:ProfileComponent,canActivate:[AuthGuard]}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
